import { Block, Node } from '@ali/4ever-cangjie';

/** uuid */
export const SET_NODE_UUID = Symbol('SET_NODE_UUID');
export function setNodeUUID(node: Node, uuid: string) {
  return {
    type: SET_NODE_UUID,
    payload: {
      node,
      uuid,
    },
  };
}

/** newlineguide */
export * from './newlineGuide';
/** blockquote */
export * from './blockquote';


/** headingGroup */
export interface ToggleFoldPayload {
  node: Block;
  fold: boolean;
}

export const TOGGLE_FOLD = Symbol('TOGGLE_FOLD');

export function toggleFold(payload: ToggleFoldPayload) {
  return {
    type: TOGGLE_FOLD,
    payload,
  };
}

